@use "global" as *;

.top-message01{
    &__img{
        width: 100%;
        height: auto;
        aspect-ratio: 1000 / 334;
        img{
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

    }
}

.top-message02{
    width: 100%;
    background-image: url(../images/top-message/top-message__contents-bg.png);
    background-position: center;
    background-size: cover;
}

.core-message{
    color: #EB6100;
    font-family: $base-font-family;
    font-weight: 900;
    font-size: rem(24);
    text-align: center;
}

@include mq (){
    .core-message{
        font-size: rem(40);
    }
}
